@import url(https://fonts.googleapis.com/css?family=Roboto);
/** Font related global styles */

html, body {
    font-family: 'Roboto', sans-serif;
    font-size: 4mm;
}

/** Component styles */
.component--LayoutTheme {
    padding: 0;
    margin: 0;
}

body {
    padding: 0 0 2em 0;
    margin: 0;
}

html {
    padding: 0;
    margin: 0;
}

/** Headers */
h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    text-shadow: 2px 2px #efefef;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.8em;
}

h3 {
    font-size: 1.6em;
}

h4 {
    font-size: 1.4em;
}

h5 {
    font-size: 1.2em;
}

h6 {
    font-size: 1em;
}

h4, h5, h6 {
    margin-top: 0.5em;
    margin-bottom: 4px;
}

/** Margins and paragraphs */
p {
    margin: 0.2em 0;
}

/** Form related global styles */
form fieldset {
    border: 0;
    margin: 0.2em;
    padding: 0;
    position: relative;
    text-align: center;
}

form fieldset input {
    display: inline-block;
    width: calc(100% - 1.4em);
    font-size: 1em;
    line-height: 1.5em;
    height: 1.5em;
    margin: 0;
    padding: 0.2em 0.5em;
    border: thin solid #e0e0e0;
}

button {
    font-size: 1.1em;
    padding: 0.3em 0.6em;
    border: thin solid #e0e0e0;
    border-radius: 2px;
}

form .buttons {
    text-align: right;
}

form textarea {
    width: calc(100% - 1.4em);
    font-size: 1em;
    margin: 0;
    border: thin solid #e0e0e0;
    padding: 0.2em 0.5em;
}

/** Link related globals */
a {
    color: #00008a;
    text-decoration: none;
}

a:visited {
    color: #00008a;
}

a:hover {
    color: #00008a;
    text-decoration: none !important;
}

/** Code styles */
code {
    display: inline-block;
    background: #fdffce;
    border-radius: 2px;
}

blockquote {
    display: inline-block;
    background: #f7f9ff;
    border-radius: 2px;
    margin: 0.5em 0;
    padding: 0.5em 1em;
    max-width: 100%;
    overflow-x: auto;
}

pre {
    max-width: 100%;
    overflow-x: auto;
}

pre code {
    padding: 0.5em 1em;
    width: calc(100% - 2em);
}

p code {
    padding: 0.2em 0.4em;
}
.component--PageLoader {
    position: fixed;
    top: 0;
    width: 100%;
    border-bottom: thin solid transparent;
    background: -webkit-linear-gradient(white, white) padding-box, -webkit-repeating-linear-gradient(right, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    background: linear-gradient(white, white) padding-box, repeating-linear-gradient(-90deg, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    -webkit-animation: ants 12s linear infinite;
            animation: ants 12s linear infinite;
    z-index: 1000;
    height: 1px;
    overflow: hidden;
}

.component--PageLoader > div {
    display: inline-block;
    width: 100%;
    font-size: 1em;
    height: 1px;
    text-align: center;
    background: #efefef;
}

@-webkit-keyframes ants {
    to {
        background-position: 100% 100%
    }
}

@keyframes ants {
    to {
        background-position: 100% 100%
    }
}
