.component--VotesAndStars {
  display: inline-block;
  width: 50px;
  margin-top: 5px;
  margin-right: 20px;
  overflow: hidden;
  vertical-align: top;
  text-align: center;
}
.component--VotesAndStars li {
  padding: 0px;
  margin: 0px;
  display: block;
  width: 100%;
  text-align: center;
  /* Magic CSS shapes */
}
.component--VotesAndStars li > div {
  display: inline-block;
  width: auto;
  height: auto;
}
.component--VotesAndStars li .votes {
  color: #6d6d6d;
  font-size: 1.4em;
  text-align: center;
  width: 100%;
}
.component--VotesAndStars li .up {
  color: #a7a0a8;
  margin-bottom: 10px;
  margin-left: 1px;
  cursor: pointer;
  width: 0;
  height: 0;
  border-left: 12.5px solid transparent;
  border-right: 12.5px solid transparent;
  border-bottom: 20px solid;
}
.component--VotesAndStars li .up.active {
  color: #6d6d6d;
}
.component--VotesAndStars li .up.active.disabled {
  color: #888aa2;
}
.component--VotesAndStars li .up.disabled {
  color: #cbcbcb;
  cursor: auto;
}
.component--VotesAndStars li .down {
  color: #a7a0a8;
  margin-top: 10px;
  margin-left: 1px;
  cursor: pointer;
  width: 0;
  height: 0;
  border-left: 12.5px solid transparent;
  border-right: 12.5px solid transparent;
  border-top: 20px solid;
}
.component--VotesAndStars li .down.active {
  color: #6d6d6d;
}
.component--VotesAndStars li .down.active.disabled {
  color: #888aa2;
}
.component--VotesAndStars li .down.disabled {
  color: #cbcbcb;
  cursor: auto;
}
.component--VotesAndStars li.star-container {
  margin-top: 1em;
  margin-left: 1px;
}
.component--VotesAndStars li .star {
  color: #a7a0a8;
  cursor: pointer;
  margin: 8.5px 0;
  position: relative;
  display: inline-block;
  width: 0px;
  height: 0px;
  border-right: 17px solid transparent;
  border-bottom: 11.9px solid;
  border-left: 17px solid transparent;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
}
.component--VotesAndStars li .star.active {
  color: #6d6d6d;
}
.component--VotesAndStars li .star.active.disabled {
  color: #888aa2;
}
.component--VotesAndStars li .star.disabled {
  color: #cbcbcb;
  cursor: auto;
}
.component--VotesAndStars li .star:before {
  border-bottom: 13.6px solid;
  border-left: 5.1px solid transparent;
  border-right: 5.1px solid transparent;
  position: absolute;
  height: 0;
  width: 0;
  top: -7.65px;
  left: -11.05px;
  display: block;
  content: "";
  -webkit-transform: rotate(-35deg);
          transform: rotate(-35deg);
}
.component--VotesAndStars li .star:after {
  position: absolute;
  display: block;
  top: 0.51px;
  left: -17.85px;
  width: 0px;
  height: 0px;
  border-right: 17px solid transparent;
  border-bottom: 11.9px solid;
  border-left: 17px solid transparent;
  -webkit-transform: rotate(-70deg);
          transform: rotate(-70deg);
  content: "";
}

